@import url(https://fonts.googleapis.com/css?family=Passion+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=PT+Sans&display=swap);
#content {
  min-height: 100vh !important; }

*, *::after, *::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: #101d2c; }

html {
  font-size: 10px !important;
  min-height: 100vh !important;
  font-family: 'PT Sans', sans-serif !important; }
  @media only screen and (max-width: 1366px) {
    html {
      font-size: 9px; } }
  @media only screen and (max-width: 700px) {
    html {
      font-size: 8px; } }

body {
  box-sizing: border-box;
  background-color: #F5F8FA !important;
  font-family: 'PT Sans', sans-serif !important;
  min-height: 100vh !important; }

#root, .root {
  min-height: 100vh !important; }

@media (min-width: 1200px) {
  .container {
    max-width: 1200px !important; } }

/* FORM TYPOGRAPHY*/
input[type=button], input[type=submit], input[type=reset], .button {
  background-color: #DC3545;
  border: none;
  color: white;
  padding: 1.3rem 4rem;
  font-weight: 600;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  text-transform: uppercase;
  font-size: 1.3rem;
  box-shadow: 0 5px 10px 0 rgba(95, 186, 233, 0.4);
  border-radius: 5px 5px 5px 5px;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  outline: none !important; }

.btn {
  outline: none !important; }
  .btn:focus, .btn:active {
    outline: none !important; }

input[type=button]:hover, input[type=submit]:hover, input[type=reset]:hover {
  background-color: #DC3545; }

input[type=button]:active, input[type=submit]:active, input[type=reset]:active {
  -webkit-transform: scale(0.95);
  transform: scale(0.95); }

input[type=text], input[type=email], input[type=password], input[type=tel], input[type=number] {
  color: #0d0d0d;
  padding: 10px 8px;
  text-decoration: none;
  display: inline-block;
  border: 1px solid #c2c2c2 !important;
  background-color: white;
  font-size: 1.5rem;
  margin: .5rem 0px;
  width: 85%;
  border: 2px solid #eee;
  -webkit-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
  border-radius: 5px 5px 5px 5px;
  outline: none !important; }

input[type=text]:focus, input[type=email]:focus, input[type=password]:focus, input[type=tel]:focus, input[type=number]:focus {
  background-color: #fff;
  outline: none !important;
  box-shadow: inset 0 -1px 0 #ddd !important; }

input:focus, select:focus, textarea:focus, textarea.form-control:focus, input.form-control:focus {
  box-shadow: inset 0 -1px 0 #ddd !important; }

input[type=text]:placeholder, input[type=email]:placeholder, input[type=password]:placeholder, input[type=tel]:placeholder, input[type=number]:placeholder {
  color: #cccccc; }

.modal-content .close {
  font-size: 2.5rem !important; }

@media only screen and (min-width: 1023px) {
  .filter-right {
    padding-right: 0 !important; } }

#carouselExampleIndicators {
  z-index: 1; }

